import React from 'react';
import PropTypes from 'prop-types';
import {magic, decodeFromUrl, encodeForUrl} from '@utils';
import {useLocation, useNavigate} from 'react-router-dom';
import Editor, {useMonaco} from '@monaco-editor/react';
import SplitPane from 'react-split-pane';
import OptionsBar from './OptionsBar';
import s from './style.scss';

const MainPage = ({setRef, isSmall, code, setCode, sql, setSql, client, setClient}) => {
  const monaco = useMonaco();
  const location = useLocation();
  const navigate = useNavigate();

  React.useEffect(() => {
    if (monaco) {
      monaco
        .languages
        .typescript
        .typescriptDefaults
        .addExtraLib(
          'declare const knex: Knex.Client;',
          'node_modules/knex/types/index.d.ts'
        );
    }
  }, [monaco]);

  React.useEffect(() => {
    const queryParams = new URLSearchParams(location.search);

    const fragment = location.hash.substring(1);

    const clientIdFromQuery = queryParams.get('c') || client.id;
    const codeFromQuery = fragment ? decodeFromUrl(fragment) : code;

    setClient({target: {value: clientIdFromQuery}});
    setCode(codeFromQuery);

  }, [location.search, location.hash]);

  const onChange = (newCode) => {
    setCode(newCode);

    const queryParams = new URLSearchParams();

    queryParams.set('c', client.id);

    navigate({
      search: queryParams.toString(),
      hash: encodeForUrl(newCode ?? code)
    });
  };

  return (
    <div
      className={s.container}
      ref={setRef}
    >
      <SplitPane split={isSmall ? 'horizontal' : 'vertical'}>
        <Editor
          theme={'vs-dark'}
          language={'typescript'}
          value={code}
          onChange={onChange}
          options={
            {
              scrollBeyondLastLine: false,
              wordWrap: 'on'
            }
          }
        />
        <Editor
          theme={'vs-dark'}
          language={'sql'}
          value={sql}
          onChange={setSql}
          options={
            {
              scrollBeyondLastLine: false,
              wordWrap: 'on',
              readOnly: true
            }
          }
        />
      </SplitPane>
      <OptionsBar />
    </div>
  );
};

const mapStore = ({EditorStore}) => {
  return {
    setRef: EditorStore.setRef,
    isSmall: EditorStore.isSmall,
    code: EditorStore.code,
    setCode: EditorStore.setCode,
    sql: EditorStore.sql,
    setSql: EditorStore.setSql,
    client: EditorStore.client,
    setClient: EditorStore.setClient
  };
};

MainPage.propTypes = {
  setRef: PropTypes.func,
  isSmall: PropTypes.bool,
  code: PropTypes.string,
  setCode: PropTypes.func,
  sql: PropTypes.string,
  setSql: PropTypes.func,
  client: PropTypes.string,
  setClient: PropTypes.func
};

export default magic(MainPage, {store: mapStore, styles: s});